:root {
  --color-primary: #3498db;
  --color-primary-light: #5dade2;
  --color-text: #2c3e50;
  --color-text-light: #34495e;
  --color-background-100: #ecf0f1;
  --color-background-500: #bdc3c7;
  --color-background-900: #95a5a6;
  --color-accent: #e74c3c;
  --color-accent-dark: #c0392b;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'Roboto', sans-serif;
  color: var(--color-text);
  background-color: var(--color-background-100);
}

/* Responsive Navigation Bar */
.nav-bar {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  height: 7.2rem;
  padding: 0 3.2rem;
  background-color: var(--color-primary);
  border-radius: 0.9rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

@media screen and (max-width: 768px) {
  .nav-bar {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, auto);
    height: auto;
    padding: 1.6rem;
    gap: 1.6rem;
  }

  .logo {
    justify-self: center;
  }

  .search {
    width: 100%;
    justify-self: stretch;
  }

  .num-results {
    justify-self: center;
  }
}

/* Main Layout */
.main {
  margin-top: 2.4rem;
  height: calc(100vh - 7.2rem - 3 * 2.4rem);
  display: flex;
  gap: 2.4rem;
  justify-content: center;
}

@media screen and (max-width: 1024px) {
  .main {
    flex-direction: column;
    height: auto;
    align-items: center;
  }

  .box {
    width: 90%;
    max-width: 100%;
    margin-bottom: 2rem;
  }
}

.box {
  width: 42rem;
  max-width: 42rem;
  background-color: #ffffff;
  border-radius: 0.9rem;
  overflow: auto;
  position: relative;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.loader {
  text-align: center;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 600;
  margin: 4.8rem;
  color: var(--color-primary);
}

.error {
  text-align: center;
  font-size: 2rem;
  padding: 4.8rem;
  color: var(--color-accent);
}

.logo {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.logo span {
  font-size: 3.2rem;
  margin-top: -0.4rem;
}

.logo h1 {
  font-size: 2.4rem;
  font-weight: 600;
  color: #fff;
}

@media screen and (max-width: 480px) {
  .logo span {
    font-size: 2.8rem;
  }

  .logo h1 {
    font-size: 2rem;
  }
}

.search {
  justify-self: center;
  border: none;
  padding: 1.1rem 1.6rem;
  font-size: 1.8rem;
  border-radius: 0.7rem;
  width: 40rem;
  transition: all 0.3s;
  color: var(--color-text);
  background-color: #ffffff;
}

@media screen and (max-width: 768px) {
  .search {
    width: 100%;
  }
}

.search::placeholder {
  color: var(--color-text-light);
}

.search:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.5);
}

.num-results {
  justify-self: end;
  font-size: 1.8rem;
  color: #ffffff;
}

.btn-toggle {
  position: absolute;
  top: 0.8rem;
  right: 0.8rem;
  height: 2.4rem;
  aspect-ratio: 1;
  border-radius: 50%;
  border: none;
  background-color: var(--color-primary);
  color: #ffffff;
  font-size: 1.4rem;
  font-weight: bold;
  cursor: pointer;
  z-index: 999;
  transition: all 0.3s;
}

.btn-toggle:hover {
  background-color: var(--color-primary-light);
}

.list {
  list-style: none;
  padding: 0.8rem 0;
  overflow: hidden;
}

.list li {
  position: relative;
  display: grid;
  grid-template-columns: 4rem 1fr;
  grid-template-rows: auto auto;
  column-gap: 2.4rem;
  font-size: 1.6rem;
  align-items: center;
  padding: 1.6rem 3.2rem;
  border-bottom: 1px solid var(--color-background-500);
}

@media screen and (max-width: 480px) {
  .list li {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .list li img {
    justify-self: center;
    margin-bottom: 1rem;
  }
}

.list.list-movies li {
  cursor: pointer;
  transition: all 0.3s;
}

.list.list-movies li:hover {
  background-color: var(--color-background-100);
}

.list img {
  width: 100%;
  grid-row: 1 / -1;
  border-radius: 0.4rem;
}

.list h3 {
  font-size: 1.8rem;
  color: var(--color-primary);
}

.list div {
  display: flex;
  align-items: center;
  gap: 2.4rem;
}

.list p {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.btn-delete {
  position: absolute;
  right: 2.4rem;
  height: 1.8rem;
  aspect-ratio: 1;
  border-radius: 50%;
  border: none;
  background-color: var(--color-accent);
  color: #ffffff;
  font-size: 0.9rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s;
}

.btn-delete:hover {
  background-color: var(--color-accent-dark);
}

.summary {
  padding: 2.2rem 3.2rem 1.8rem 3.2rem;
  border-radius: 0.9rem;
  background-color: var(--color-background-100);
  box-shadow: 0 1.2rem 2.4rem rgba(0, 0, 0, 0.1);
}

.summary h2 {
  text-transform: uppercase;
  font-size: 1.6rem;
  margin-bottom: 0.6rem;
  color: var(--color-primary);
}

.summary div {
  display: flex;
  align-items: center;
  gap: 2.4rem;
  font-size: 1.6rem;
  font-weight: 600;
}

.summary p {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.details {
  line-height: 1.4;
  font-size: 1.4rem;
}

.details header {
  display: flex;
}

@media screen and (max-width: 600px) {
  .details header {
    flex-direction: column;
    align-items: center;
  }
}

.details section {
  padding: 4rem;
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}

@media screen and (max-width: 480px) {
  .details section {
    padding: 2rem;
  }
}

.details img {
  width: 33%;
  border-radius: 0.9rem;
}

@media screen and (max-width: 600px) {
  .details img {
    width: 60%;
    margin-bottom: 1rem;
  }
}

.details-overview {
  width: 100%;
  padding: 2.4rem 3rem;
  background-color: var(--color-background-100);
  display: flex;
  flex-direction: column;
  gap: 1.4rem;
  border-radius: 0.9rem;
}

.details-overview h2 {
  font-size: 2.4rem;
  margin-bottom: 0.4rem;
  line-height: 1.1;
  color: var(--color-primary);
}

@media screen and (max-width: 480px) {
  .details-overview h2 {
    font-size: 2rem;
  }
}

.details-overview p {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.rating {
  background-color: var(--color-background-100);
  border-radius: 0.9rem;
  padding: 2rem 2.4rem;
  margin-bottom: 0.8rem;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
}

.btn-add {
  background-color: var(--color-primary);
  color: #ffffff;
  border: none;
  border-radius: 10rem;
  font-size: 1.4rem;
  padding: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s;
}

.btn-add:hover {
  background-color: var(--color-primary-light);
}

.btn-back {
  position: absolute;
  top: 0.6rem;
  left: 0.6rem;
  height: 3.2rem;
  aspect-ratio: 1;
  border-radius: 50%;
  border: none;
  background-color: #ffffff;
  color: var(--color-primary);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  font-family: sans-serif;
  font-size: 2.4rem;
  font-weight: bold;
  cursor: pointer;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s;
}

.btn-back:hover {
  background-color: var(--color-primary);
  color: #ffffff;
}

.loading-bar {
  width: 100%;
  height: 30px;
  background-color: var(--color-background-500);
  border-radius: 4px;
  overflow: hidden;
}

.bar {
  height: 100%;
  width: 0;
  background-color: var(--color-primary);
  animation: progress 2s linear infinite;
}

@keyframes progress {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}

@media screen and (max-width: 480px) {
  html {
    font-size: 50%;
  }
}